ul {
  padding-inline-start: 0px;
}

body {
  background-color: rgb(63, 63, 105);
  text-align: center;
  padding: 10px;
  box-sizing: border-box;
  color: white;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.flip-card {
  padding: 0%;
  background-color: transparent;
  width: 300px;
  height: 300px;
  perspective: 1000px;
  margin-bottom: 10px;
  margin-left: auto;
  margin-right: auto;
}

p {
  color: white;
  font-size: 40px;
  margin: 0px;
  padding: 0px;
}

.flip-card-inner {
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.6s;
  transform-style: preserve-3d;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

.flip-card:hover .flip-card-inner {
  transform: rotateY(180deg);
}

.flip-card-front,
.flip-card-back {
  align-items: center;
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.flip-card-front {
  background-color: #bbb;
  color: black;
}

.flip-card-back {
  background-color: #2980b9;
  color: white;
  transform: rotateY(180deg);
}
